import { useRef, useEffect } from 'react';
import * as THREE from 'three';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
import './TextOverlay.module.css';

const TextOverlay = ({ emotionsData, scene, camera }) => {
  const textMeshRef = useRef([]);

  useEffect(() => {
    if (!camera) {
      console.error('Camera is not defined');
      return;
    }

    const loader = new FontLoader();
    loader.load('/fonts/SFProDisplay_Regular.json', font => {
      const createText = (emotion, position, animationParams) => {
        const radius = 250; // Radius for circular text
        const textLength = emotion.text_input.length;
        const angleIncrement = (2 * Math.PI) / textLength;

        const textGroup = new THREE.Group(); // Create a group for text characters

        // Create text meshes for each character
        for (let i = 0; i < textLength; i++) {
          const charGeometry = new TextGeometry(emotion.text_input.charAt(i), {
            font: font,
            size: 45, // Text size
            depth: 1, // Text depth
            curveSegments: 24,
            bevelEnabled: false,
            bevelThickness: 0,
            bevelSize: 0.0,
            bevelOffset: 0,
            bevelSegments: 0,
          });

          const charMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
          const charMesh = new THREE.Mesh(charGeometry, charMaterial);

          // Calculate character position on the circle
          const angle = i * angleIncrement;
          const x = radius * Math.cos(angle);
          const y = radius * Math.sin(angle);

          charMesh.position.set(x, y, 0);

          // Ensure the character is facing outward from the circle
          charMesh.rotation.z = angle + Math.PI / 2;

          textGroup.add(charMesh);
        }

        textGroup.position.copy(position);
        textGroup.userData = {
          animationParams,
          originalPosition: textGroup.position.clone(),
          rotationSpeed: 100000 // Adjust the rotation speed as needed
        };

        scene.add(textGroup);
        textMeshRef.current.push(textGroup);
      };

      emotionsData.forEach(emotion => {
        const latitudeInRadians = THREE.MathUtils.degToRad(emotion.latitude);
        const longitudeInRadians = THREE.MathUtils.degToRad(emotion.longitude);
        const radius = 1000;

        const x = radius * Math.cos(latitudeInRadians) * Math.sin(longitudeInRadians);
        const y = radius * Math.sin(latitudeInRadians);
        const z = radius * Math.cos(latitudeInRadians) * Math.cos(longitudeInRadians);

        const position = new THREE.Vector3(x, y, z);

        const animationParams = {
          speed: Math.random() * 0.005 + 0.001,
          amplitudeX: Math.random() * 50 + 10,
          amplitudeY: Math.random() * 50 + 10,
          amplitudeZ: Math.random() * 50 + 10,
          offsetX: Math.random() * Math.PI * 2,
          offsetY: Math.random() * Math.PI * 2,
          offsetZ: Math.random() * Math.PI * 2,
        };

        createText(emotion, position, animationParams);
      });

      const animate = () => {
        requestAnimationFrame(animate);

        let time = Date.now() * 0.025;

        textMeshRef.current.forEach(mesh => {
          if (mesh) {
            const { animationParams, originalPosition, rotationSpeed } = mesh.userData;
            const factor = 7.5;
            mesh.position.y = originalPosition.y + Math.sin(time * animationParams.speed + animationParams.offsetY) * animationParams.amplitudeY * factor;
            mesh.position.x = originalPosition.x + Math.sin(time * animationParams.speed + animationParams.offsetX) * animationParams.amplitudeX * factor;
            mesh.position.z = originalPosition.z + Math.sin(time * animationParams.speed + animationParams.offsetZ) * animationParams.amplitudeZ * factor;
            
            // Apply slow continuous rotation to the entire text group
            mesh.rotation.z += rotationSpeed;

            mesh.lookAt(camera.position);
          }
        });
      };

      animate();
    });

    // Clean up text meshes on unmount
    const currentTextMeshes = textMeshRef.current;
    return () => {
      currentTextMeshes.forEach(mesh => scene.remove(mesh));
    };
  }, [emotionsData, scene, camera]);

  return null;
};

export default TextOverlay;