import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import WelcomePage from './components/WelcomePage/WelcomePage';
import HomePage from './components/HomePage/HomePage';
import VisualizationPage from './components/VisualizationPage/VisualizationPage';
import LoginPage from './components/LoginPage/LoginPage';
import SignupPage from './components/SignupPage/SignupPage';
import PrivateRoute from './components/PrivateRoute';
import Dashboard from './components/Dashboard/Dashboard';

import EmotionList from './components/EmotionList/EmotionList';
import styles from './styles/App.module.css';
import './styles/Normalize.css';

function App() {
  const [emotion, setEmotion] = useState('');

  return (
    <Router>

      <div className={styles.appContainer}>
        <Routes>
          <Route path="/" element={<WelcomePage />} />
          <Route path="/home" element={<HomePage emotion={emotion} setEmotion={setEmotion} />} />
          <Route path="/visualization" element={<VisualizationPage />} />
          <Route path="/emotion-list" element={<EmotionList />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/signup" element={<SignupPage />} />
          <Route path="/dashboard" element={<PrivateRoute />}>
            <Route path="/dashboard" element={<Dashboard />} />
          </Route>
        </Routes>
      </div>
    </Router>
  );
}

export default App;